.sticky-bin {
  .bin-container {
    flex-direction: column;
    margin: 1rem 1rem;

    //height: 10rem;

    .title {
      justify-content: center;
      h3 {
        padding-bottom: 0.5rem;
        margin: 0;
      }
    }

    form {
      display: flex;
      flex-direction: column;
      textarea {
        transition: background-image 0.2s ease-in-out;

        &.loading {
          opacity: 0.6;
          background-image: url("../../../assets/gifs/1487.gif");
          background-repeat: no-repeat;
          background-position: center;
        }
      }
    }
  }
}

@media only screen and (max-width: 48rem) {
  .sticky-bin {
    flex-direction: column;
    flex: 1 1 100%;
    .bin-container {
      .title {
        justify-content: center;
      }

      form {
        textarea {
          flex: 1 1 100%;
        }
      }
    }
  }
}
